@import url(https://fonts.googleapis.com/css2?family=Padauk:wght@400;700&display=swap);
body {
    background: #e3f2f2;
    color: #2a2946;
    font-family: "Padauk", sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    margin: 0;
}
.title {
    font-size: 2.5rem;
    text-align: center;
    width: 150px;
    border-top: 1px solid #2a2946;
    border-bottom: 1px solid #2a2946;
    margin-left: auto;
    margin-right: auto;
}
@media (max-width: 576px) {
    .title {
        font-size: 2rem;
        width: 100px;
    }
}
.tour-container {
    max-width: 600px;
}
.tour {
    background: white;
    margin-bottom: 2rem;
    box-shadow: 1px 2px 4px lightgray;
}
.tour__image {
    width: 100%;
    height: 300px;
}
@media (max-width: 576px) {
    .tour__image {
        height: 200px;
    }
}

.tour__image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.tour__details {
    padding: 1rem;
}
.tour__name-and-price {
    position: relative;
}
.tour__name-and-price p {
    font-size: 1.4rem;
    font-weight: 700;
    margin-top: 0;
    margin-bottom: 1rem;
}
.tour__name-and-price span {
    position: absolute;
    right: 0;
    top: 0;
    font-weight: bold;
    font-size: 1.4rem;
    color: #30627c;
}
@media (max-width: 576px) {
    .tour__name-and-price p,
    .tour__name-and-price span {
        font-size: 1.1rem;
    }
}
@media (max-width: 370px) {
    .tour__name-and-price p,
    .tour__name-and-price span,
    .tour__description {
        font-size: 0.9rem;
    }
}
.tours__button {
    margin: 0 auto;
    display: block;
    font-size: 1rem;
    background: none;
    outline: none;
    border: none;
    font-weight: bold;
    color: #b05e44;
    border: 1px solid #b05e44;
    padding: 5px 15px;
    cursor: pointer;
}
.loading {
    font-size: 1.5rem;
    text-align: center;
}
.read-more-btn {
    cursor: pointer;
    color: #30627c;
    font-weight: bold;
    border: none;
    background: none;
    text-decoration: underline;
}

